:root{
    --letterColor:#e3e3e3
}
.keyboard{
    width:90%;
    height:25%;
  
   display: grid;
   grid-template-columns: repeat(13,1fr);
   grid-template-rows: repeat(2,1fr);
   gap: 5px;
 padding: 0 5px;
}
.letter{
    border:1px solid black;
    font-family: 'Courier New', Courier, monospace;
    font-size: 25px;
   
    font-weight: 800;
    text-align: center;
    border-radius: 5px;
    background-color: var(--letterColor);
   
    
   
}
.letterH:hover{
    transform: scale(1.3);
    cursor: pointer;
}