
.game{
    background-color: #0cbaba;
    background-image: linear-gradient(315deg, #0cbaba 0%, #380036 74%);
    display: flex;
    flex-direction: column;
    height: 90vh;
    width:50vw;
   
    border-radius: 20px;
    position: relative;
    

}
.gameForm{
    display: flex;
    flex-direction: column;
    position: absolute;
    top:50%;
    left: 35%;
    border: 1px solid gray;
    padding: 15px 15px;
    border-radius: 30px;
    background-color: #0cbaba;
    background-image: linear-gradient(315deg, #0cbaba 0%, #380036 74%);
}
.gameForm >input{
    text-indent: 7px;
    border-radius: 30px;
    background-color: transparent;
    border-top:none;
    border-left:none;
    border-right:none;
    color: aliceblue;
    margin-bottom: 5px;
}
.gameForm >input:focus{
   outline: none;
}

.gameForm >textarea{
    
    border-radius: 20px;
    background-color: transparent;
    border-top:none;
    border-right:none;
    
    color: aliceblue;
    margin-bottom: 5px;
    padding: 10px;
    resize: none;
}

.gameForm >textarea:focus{
    outline: none;
 }
 
 .gameForm >button{
    width: 50%;
    align-self: center;
 }