
.chatBox{
    display: flex;
    flex-direction: column;
    height: 90vh;
    width:23vw;
  
    border-radius: 30px;
    background-color: #0cbaba;
    background-image: linear-gradient(315deg, #0cbaba 0%, #380036 74%);
    margin:0 5%;
    display: flex;
    flex-direction: column;
   
   
}
.messageBox{
    height:92%;
   
    font-size: 15px;
    border-top:1px solid #0cbaba;
    padding:10px;
    overflow-y: auto;
}
.chatBoxSendMsg{
    display: flex;
    justify-content: flex-start;
    height: 8%;
    border-top: 1px solid #0cbaba;
}
.chatBoxSendMsg >input{
   background-color: transparent;
   color:white;
   
 border: none;
   width:75%;
 
   text-indent: 20px;
   font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
   font-size: medium;
}

 .chatBoxSendMsg >input:hover{
     outline:none;
     border: none;
 }
 .chatBoxSendMsg >input:focus{
    outline:none;
    border: none;
}
.chatBoxSendBtn{

    background-color:transparent;
    border:2px solid #0cbaba;
    cursor: pointer;
   
    border-bottom-right-radius: 30px;
    padding: 0px 2px;
    font-weight: 700;
    width:25%;
    height: 100%;

   
}
.chatBoxSendBtn:hover{
transform: scale(1.05);
}
.username{
    text-align: center;
    color:aliceblue;
}