.wordDisplayer{
    width:auto;
    height:20%;
    border:1px solid white;
    border-radius: 15px;
    padding: 3px 10px;
    margin-bottom: 5%;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
 
  display: flex;

}
.wordLetter{
    
     
    text-align: center;
    font-family: 'Courier New', Courier, monospace;
    font-size: 30px;
    cursor: pointer;
    font-weight: 800;
    border-radius: 5px;

    background-color: #a40606;
    background-image: linear-gradient(315deg, #a40606 0%, #d98324 74%);
    
    
   
    margin-left:8px ;
    height:70%;
    width: 25px;
}