.login{
    color:wheat;
    width:60%;
    height:50%;
    background-color: #0cbaba;
    background-image: linear-gradient(315deg, #0cbaba 0%, #380036 74%);
    display: flex;
    padding:20px;
    border-radius: 10px;
    -webkit-box-shadow: 5px 5px 15px 5px #000000; 
box-shadow: 5px 5px 15px 5px #000000;
 padding-left: 50px;
 padding-top: 25px;
 align-items: center;
    
}
.loginRooms{
    flex:1;

    
}
.loginForm{
  
    flex:2;
    height:fit-content;
    display: flex;
    flex-direction: column;
    border:1px solid gray;
    border-radius: 30px;
    padding: 10px;
    height: 95%;
    
    
}
.loginForm >input{
    background-color: transparent;
    border: none;
    border-bottom-right-radius: 30px;
    padding:4px 5px;
   text-indent: 5px;
   color:white;
   padding: 0px 2px;
   font-size: medium;
   border-bottom: 1px solid gray;
   width: 80%;
   margin-bottom: 2%;
   margin-top: 2%;
}
.loginForm >input:hover{
   outline: none;
}
.loginForm >input:focus{
    outline: none;
 }
.loginRooms{
    display: flex;
   flex-direction: column;
   height: 95%;
    margin-left: 2%;
  
}
.roomName{
  color: whitesmoke;
  font-size: 18px;
  font-weight: 500;
  
  text-transform: capitalize;
}
.loginRoom{
   
    background-color: transparent;
    border: none;
    border-bottom-right-radius: 20px;
    padding:4px 5px;
   text-indent: 8px;
   color:white;
   padding: 0px 2px;
   font-size: medium;
   font-weight: 480;
   border-bottom: 1px solid gray;
   width: 80%;
   margin-bottom: 2%;
   margin-top: 2%;
   cursor: pointer;
   border-top-left-radius: 5px;
   border-bottom-left-radius: 5px;
 
   -webkit-box-shadow: 5px 5px 15px 5px #000000; 
box-shadow: 5px 5px 15px 5px #000000;
margin-left: 9%;
}
.loginRoom:hover{
    transform: scale(1.05);
    transition: ease;
    
  
}
.loginRoomsWrapper{
    margin-top: 2%;
    overflow-y: auto;
border:1px solid gray;
    align-self: center;
    height:90%;
    width:90%;
  
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    
    align-items:flex-start;
    padding: 10px;
}
.loginBtn{
    background-color: transparent;
    margin-top: 20px;
    cursor: pointer;
    font-size: medium;
    font-weight: 500;
    color:aliceblue;
    width:50%;
    align-self: center;
    border-top: none;
    border-left: 1px solid gray;
    border-right: 1px solid gray;
    border-radius: 30px;
    padding-bottom: 5px;
    
}
.loginBtn:hover{
    background-color: #380036;
}
